body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.tableHead li:nth-of-type(1), .tableFooter li:nth-of-type(1), .tableRow .tableCol:nth-of-type(1) {
  width: 25%;
  padding-left: 10px; }

.tableHead li:nth-of-type(2), .tableFooter li:nth-of-type(2), .tableRow .tableCol:nth-of-type(2) {
  width: 10%; }

.tableHead li:nth-of-type(3), .tableFooter li:nth-of-type(3), .tableRow .tableCol:nth-of-type(3) {
  width: 10%; }

.tableHead li:nth-of-type(4), .tableFooter li:nth-of-type(4), .tableRow .tableCol:nth-of-type(4) {
  width: 10%; }

.tableHead li:nth-of-type(5), .tableFooter li:nth-of-type(5), .tableRow .tableCol:nth-of-type(5) {
  width: 10%; }

.tableHead li:nth-of-type(6), .tableFooter li:nth-of-type(6), .tableRow .tableCol:nth-of-type(6) {
  width: 15%; }

.tableHead li:nth-of-type(7), .tableFooter li:nth-of-type(7), .tableRow .tableCol:nth-of-type(7) {
  width: 10%; }

.tableHead li:nth-of-type(8), .tableFooter li:nth-of-type(8), .tableRow .tableCol:nth-of-type(8) {
  width: 10%; }

select {
  border: 1px solid #ddd;
  border-radius: 5px;
  height: 30px;
  background-color: #fff;
  font-size: 16px; }

input {
  border: 1px solid #ddd;
  border-radius: 5px;
  height: 30px;
  padding: 0 10px;
  font-size: 16px;
  text-align: center; }

.topInfo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px; }
  .topInfo input {
    width: 50px; }

.bottomInfo {
  display: flex;
  align-items: center;
  padding: 20px; }
  .bottomInfo input {
    width: 50px; }

.tableArea {
  padding: 20px; }

.tableHead {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 0; }
  .tableHead li {
    text-align: center; }

.tableFooter {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 0; }
  .tableFooter li {
    text-align: center; }

.tableRow {
  width: 100%;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  padding: 10px 0; }
  .tableRow:first-of-type {
    border-top: 1px solid #eee; }
  .tableRow:hover {
    background-color: #f7f7f7; }

.tableCol {
  display: flex;
  align-items: center;
  justify-content: center; }

.oil_select {
  justify-content: flex-start; }
  .oil_select select {
    width: 60%; }

.oil_percentage input {
  width: 80%; }

.delete_btn {
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 16px;
  border-radius: 50%;
  border: 1px solid #ddd;
  text-decoration: none;
  color: #ddd;
  cursor: pointer; }
  .delete_btn:hover {
    color: #cc0000;
    border-color: #cc0000; }

.addRowBtnArea {
  display: flex;
  align-items: center;
  justify-content: flex-end; }
  .addRowBtnArea a {
    cursor: pointer;
    padding: 8px 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    text-decoration: none;
    color: #333; }

